.checkboxes {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 15px;

  &__wrapper {
    position: relative;
  }

  &__checkbox-label {
    font-size: 14px;
    line-height: 16px;
  }

  .filter-modal &__checkbox-label {
    display: block;
    width: 80%;
    padding: 4px 0;
    text-align: center;
    background-color: #f0f0f4;
    border-radius: 50px;
  }

  .filter-selection__fieldset--stocks &__checkbox-label {
    display: block;
    width: auto;
    padding: 6px 15px;
    color: #bdbec2;
    text-align: center;
    background-color: #f0f0f4;
    border-radius: 0;
  }

  .filter-modal &__checkbox:checked + &__checkbox-label {
    color: #fff;
    background-color: #ff7931;
  }

  .filter-selection & {
    margin-bottom: 16px;
  }

  .filter-selection &__checkbox-indicator {
    position: relative;
    display: inline-block;
    width: 14px;
    height: 14px;
    vertical-align: middle;
    border: 1px solid #cdcdcd;
  }

  .filter-selection &__checkbox:checked + &__checkbox-indicator {
    background-color: #ff7931;
    border: none;

    &::before,
    &::after {
      position: absolute;
      content: "";
      top: 8px;
      height: 1px;
      background-color: #fff;
    }

    &::before {
      left: 2px;
      width: 4px;
      transform: rotate(45deg);
    }

    &::after {
      top: 7px;
      left: 4px;
      width: 8px;
      transform: rotate(-45deg);
    }
  }

  .filter-selection__fieldset--stocks & {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 0;
  }

  .filter-selection__fieldset--stocks &__checkbox:checked + &__checkbox-label {
    color: #fff;
    background: linear-gradient(91.1deg, #9710e9 1.17%, #fd8d43 99.31%);
  }
}
