.input-text {
  width: 100%;
  padding: 8px 0;
  padding-left: 20px;
  font-size: 12px;
  line-height: 14px;
  color: #bdbec2;
  border: 1px solid #e0e0e0;

  &:focus {
    font-weight: bold;
    color: #000;
    border: 1px solid #ff7931;
    outline: none;
  }

  .filter-selection & {
    margin-bottom: 20px;
  }
}
