@charset "UTF-8";
html {
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  font-size: 20px;
  line-height: 23px;
  font-family: Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  color: #48494D;
  background-color: #fff;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.container {
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 16px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 20px;
  }
}

.button {
  display: block;
  font-size: 15px;
  line-height: 17px;
  font-weight: bold;
  text-align: center;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  text-decoration: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
}
.button__favorite {
  z-index: 10;
  width: 29px;
  height: 26px;
  align-self: center;
  background-image: url("../img/icons/favorites.svg");
}
.button__favorite--active {
  background-image: url("../img/icons/favorites-active.svg");
}
.button__favorite:hover {
  opacity: 0.7;
}
.button__season {
  background-repeat: no-repeat;
}
.button__season--summer {
  width: 31px;
  height: 31px;
  background-image: url("../img/icons/sun.svg");
}
.button__season--winter {
  width: 28px;
  height: 31px;
  background-image: url("../img/icons/snowflake.svg");
}
.button--show-more {
  max-width: 252px;
  margin: 0 auto;
  padding: 17px 0 16px;
  color: #bdbec2;
  background-color: #f0f0f4;
}
.button--input {
  color: #fff;
  background-color: #ff7931;
}
.button:hover {
  opacity: 0.7;
}
.button:active {
  opacity: 1;
}
@media (min-width: 768px) {
  .product__img .button__season--winter {
    width: 36px;
    height: 40px;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.radio-buttons__label-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  color: #000;
  cursor: pointer;
}
.radio-buttons__input + .radio-buttons__indicator {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  width: 14px;
  height: 14px;
  border: 1px solid #cdcdcd;
  border-radius: 2px;
}
.radio-buttons__input + .radio-buttons__indicator:hover {
  opacity: 0.7;
}
.radio-buttons__input:checked + .radio-buttons__indicator {
  border-color: #ff7931;
  background-color: transparent;
}
.radio-buttons__input:checked + .radio-buttons__indicator::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 6px;
  height: 6px;
}
.filter-modal .radio-buttons {
  display: block;
}
.filter-selection .radio-buttons__input + .radio-buttons__indicator {
  top: 1px;
  left: 0;
}
.filter-modal .radio-buttons__input + .radio-buttons__indicator {
  top: 15px;
  right: 0;
}
.filter-modal .radio-buttons__input:checked + .radio-buttons__indicator {
  background-color: #ff7931;
  border: none;
}
.filter-modal .radio-buttons__input:checked + .radio-buttons__indicator::before, .filter-modal .radio-buttons__input:checked + .radio-buttons__indicator::after {
  position: absolute;
  content: "";
}
.filter-modal .radio-buttons__input:checked + .radio-buttons__indicator::before {
  top: 7px;
  left: 2px;
  width: 4px;
  height: 1px;
  background-color: #fff;
  transform: rotate(45deg);
}
.filter-modal .radio-buttons__input:checked + .radio-buttons__indicator::after {
  top: 6px;
  left: 4px;
  width: 8px;
  height: 1px;
  background-color: #fff;
  transform: rotate(-45deg);
}
.filter-selection .radio-buttons {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  row-gap: 15px;
}
.filter-selection .radio-buttons__label-wrapper {
  padding-left: 30px;
}
.filter-selection .radio-buttons__label-wrapper:nth-child(2n) {
  width: 50%;
}
.filter-selection .radio-buttons__input:checked + .radio-buttons__indicator {
  border: 2px solid #ff7931;
}
.filter-selection .radio-buttons__input:checked + .radio-buttons__indicator::before {
  position: absolute;
  top: 1px;
  left: 1px;
  width: 8px;
  height: 8px;
  background-color: #ff7931;
}

.page-header__container {
