.slick-carousel--no-tab {
  padding: 0 25px;
}

.slick-slider {
  position: relative;
  display: block;
  margin: 0 -15px;
  box-sizing: border-box;
  user-select: none;
  touch-action: pan-y;
}

.slick-track,
.slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track::before, .slick-track::after {
  content: "";
  display: table;
}
.slick-track::after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  margin: 0 15px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: flex;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-slide > * {
  width: 100%;
}

.slick-arrow.slick-hidden {
  display: none;
}

.slick-dots {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
  margin-top: 28px;
  margin-bottom: 40px;
}

.slick-dots li {
  width: 14px;
  height: 14px;
  margin: 0 14px;
  background-color: #ccc;
  border-radius: 50%;
}

.slick-dots .slick-active {
  background-color: #ff7931;
}

.slick-dots button {
  font-size: 0;
  background-color: transparent;
  border: none;
}

.slick-dots li:not(:nth-child(-n+3)) {
  display: none;
}

@media (min-width: 1024px) {
  .slick-slider {
    margin-bottom: 35px;
  }

  .slick-arrow {
    position: absolute;
    top: 45%;
    z-index: 1001;
    width: 36px;
    height: 36px;
    font-size: 0;
    background-color: #fff;
    background-repeat: no-repeat;
    background-position: center;
    border: none;
    border-radius: 50%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  .slick-arrow:hover {
    background-color: #d7d9df;
  }

  .slick-prev {
    left: 20px;
    background-image: url("../img/icons/arrow-dark-left.svg");
  }

  .slick-next {
    right: 20px;
    background-image: url("../img/icons/arrow-dark-right.svg");
  }
}
@media (min-width: 1232px) {
  .slick-prev {
    left: -30px;
  }

  .slick-next {
    right: -30px;
  }
}
