.hamburger {
  position: absolute;
  content: "";
  top: 32px;
  left: 16px;
  display: block;
  width: 33px;
  height: 30px;
  background-color: #fff;
  border: none;
  cursor: pointer;
}
.no-js .hamburger {
  display: none;
}
.hamburger::before, .hamburger::after {
  position: absolute;
  content: "";
}
.hamburger::after {
  top: 3px;
  left: 0;
  width: 33px;
  height: 3px;
  background-color: #c4c4c4;
  box-shadow: 0 12px 0 0 #c4c4c4, 0 24px 0 0 #c4c4c4;
  transition: all 0.3s ease-in;
}
.hamburger--opened::before, .hamburger--opened::after {
  top: 13px;
  left: 0;
  width: 33px;
  height: 3px;
  background-color: rgba(0, 0, 0, 0.4);
  transition: all 0.3s ease-in;
}
.hamburger--opened::before {
  transform: rotate(45deg);
}
.hamburger--opened::after {
  box-shadow: none;
  transform: rotate(-45deg);
}

@media (min-width: 1024px) {
  .hamburger {
    display: none;
  }
}
